import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { ListItemPage } from '../list-item/list-item';
import { ContactPage } from '../contact/contact';

@Component({
  selector: 'page-list',
  templateUrl: 'list.html'
})
export class ListPage {
  selectedItem: any;
  villes: string[];
  items: Array<{name: string}>;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    // If we navigated to this page, we will have an item available as a nav param
    this.selectedItem = navParams.get('item');

    // Let's populate this page with some filler content for funzies
    this.villes = ['Paris', 'Rennes', 'Bordeaux', 'Riom', 'Clermont-Ferrand', 'Amiens',
    'Strasbourg', 'Toulon', 'Roubaix', 'Dinand'];

    this.items = [];
    for (let i = 1; i < this.villes.length; i++) {
      this.items.push({
        name: this.villes[i]
      });
    }
  }

  itemTapped(event, item) {
    // That's right, we're pushing to ourselves!
    // Not anymore.
    this.navCtrl.push(ListItemPage, {
      item: item
    });
  }

  addItem() {
    alert("Mes excuses, cette fonctionnalité n'est pas implémentée.")
    // this.navCtrl.push(AddNewElementPage);
    // Tried, but it doesn't work. :(
  }
}



