export class State{
    //Properties
    private play: boolean = true;
    private backward: boolean = false;
    private stop: boolean = false;
    
    //Methods
    setPlay() {
        this.stop = true;
        this.play = this.backward = false;
    }

    setStop() {
        this.stop = false;
        this.backward = this.play = true;
    }

    setBackward() {
        this.play = true;
        this.backward = this.backward = false;
    }
}


