export class Timer{
    // Properties
    private secondes: number = 0;
    private minutes: number = 0;
    private heures: number = 0;
    private miliseconds: number = 0;
    private totalSecondes: number = 0;
    private timer;

    //Methods
    start(){
        this.timer = setInterval( () => {
            // this.minutes = Math.floor(++this.totalSecondes / 60);
            // this.secondes = this.totalSecondes - this.minutes * 60;
            
            this.miliseconds++;
            if (this.miliseconds == 10){
                this.miliseconds = 0;
                this.secondes++;
            }
            if (this.secondes == 60) {
                this.secondes = 0;
                this.minutes++;
            }
            if (this.minutes == 60) {
                this.minutes = 0;
                this.heures = 1;
            }
        }, 100);
    }

    stop(){
        clearInterval(this.timer);
    }

    reset(){
        this.totalSecondes = this.minutes = this.secondes = this.miliseconds = this.heures =  0;
    }
}


