export class TimeDate{
    // Properties
    private day: number = 0;
    private month: number = 0;
    private year: number = 0;
    private wday: number = 0;

    // Methods
    updateTiming(){
        let time: Date = new Date();
        this.day = time.getUTCDate();
        this.month = time.getUTCMonth() + 1;
        this.year = time.getUTCFullYear();
        this.wday = time.getUTCDay();
    }

    // Return the objects properties value
    returnDay(){
        return this.day;
    }

    returnMonth(){
        return this.month;
    }

    returnYear(){
        return this.year;
    }

    returnWday(){
        return this.wday;
    }

    // To set boundaries for the input
    checkCoherence(){
        if (this.day > 31){
            this.day = 1;
        }

        if (this.day < 1){
            this.day = 31;
        }

        if (this.month > 12){
            this.month = 1;
        }

        if (this.month < 1){
            this.month = 12;
        }
    }
}


