import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { TimeDate } from '../../app/dateTime';
import { TimeClock } from '../../app/dateClock';

@Component({
  selector: 'page-clock',
  templateUrl: 'clock.html'
})
export class ClockPage {

  // Creating our 2 objects: Date & Clock
  private myClock: TimeClock = new TimeClock();
  private myDate: TimeDate = new TimeDate();

  // To display either today or not today
  private today: boolean = true;
  private notToday: boolean;

  private dayToday: string;
  private monthToday: string;

  private language: string = 'FR';

  // To check the seasons
  private winter: boolean = false; // 22 + 9 + 2017 - 20 + 12 + 2017
  private summer: boolean = false; // 21 + 12 + 2017 - 19 + 3 + 2017
  private spring: boolean = false; // 20 + 3 + 2018 - 20 + 6 + 2018
  private fall: boolean = false; // 21 + 6 + 2018 - 22 + 9 + 2018

  // For debug purposes, ignore
  private test;
  private test2;

  constructor(public navCtrl: NavController) {
    this.updateTime();
    this.myDate.updateTiming();
    this.displaySeason();
    this.checkTime();
  }
  
  strDay(lang: string): string{
    let today = this.myDate.returnWday() - 1;
    let days = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'];
    let jours = ['Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi', 'Dimanche'];

    if (lang == 'FR'){
      return jours[today];
    }
    if (lang == 'EN'){
      return days[today];
    }
  }

  strMonth(lang: string): string{
    let today = this.myDate.returnMonth() - 1;
    let months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
    let mois = ['Janvier', 'Février', 'Mars', 'Avril', 'Mai', 'Juin', 'Juillet', 'Août', 'Septembre', 'Octobre', 'Novembre', 'Décembre']

    if (lang == 'FR'){
      return mois[today];
    }
    if (lang == 'EN'){
      return months[today];
    }
  }

  // Check if it's today, duh.
  checkToday(){
    let myTime: Date = new Date();
    let day: string = this.myDate.returnDay().toString()
    let month: string = (this.myDate.returnMonth()).toString()
    let year: string = this.myDate.returnYear().toString();
    let curDate: string = day + month + year;
    let curDateCheck: string = myTime.getUTCDate().toString() + (myTime.getUTCMonth() + 1).toString() + myTime.getUTCFullYear().toString();

    this.test = curDate;
    this.test2 = curDateCheck;

    if (curDate != curDateCheck){
      this.today = false;
      this.notToday = true;
    } else {
      this.today = true;
      this.notToday = false;
    }
  }

  // Update the current time every second 
  updateTime(){
    setInterval( () => {
      this.myClock.updateTiming();
    }, 1000);
  }

  // Safeguard limit for the input and display the season accordingly, and also check if the date is today
  checkTime(){
    setInterval( () => {
      this.myDate.checkCoherence();
      this.displaySeason();
      this.checkToday();
      this.dayToday = this.strDay(this.language);
      this.monthToday = this.strMonth(this.language);
    }, 50);
  }

  // Check the season
  displaySeason(){
    let day: string = this.myDate.returnDay().toString()
    let month: string = this.myDate.returnMonth().toString()
    let year: string = this.myDate.returnYear().toString();

    if (day.length == 1){
      day = "0" + day;
    }

    if (month.length == 1){
      month = "0" + month;
    }

    // Create a number from the concatenation of of year/month/day; it'll be used for checking dates
    let curDate: number = parseInt(year + month + day);

    // Set the boundaries for each season
    let minWinter: number = parseInt((parseInt(year) - 1).toString() + "12" + "21");
    let maxWinter: number = parseInt(year.toString() + "03" + "19");

    let minSpring: number = parseInt(year.toString() + "03" + "20");
    let maxSpring: number = parseInt(year.toString() + "06" + "20");

    let minSummer: number = parseInt(year.toString() + "06" + "21");
    let maxSummer: number = parseInt(year.toString() + "09" + "22");

    let minFall: number = parseInt(year.toString() + "09" + "23");
    let maxFall: number = parseInt(year.toString() + "12" + "20");

    // Some debug
    // this.test = minFall;
    // this.test2 = maxFall;
    // this.curDate = curDate;
    
    // Set the right season, and disable the others
    if (curDate >= minWinter && curDate <= maxWinter){
      this.winter = true;
      this.spring = this.summer = this.fall = false;
    }
    if (curDate >= minSummer && curDate <= maxSummer){
      this.summer = true;
      this.spring = this.winter = this.fall = false;
    }
    if (curDate >= minSpring && curDate <= maxSpring){
      this.spring = true;
      this.winter = this.summer = this.fall = false;
    }
    if (curDate >= minFall && curDate <= maxFall){
      this.fall = true;
      this.spring = this.summer = this.winter = false;
    }
  }
}



