import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { ContactPage } from '../pages/contact/contact';
import { ListItemPage } from '../pages/list-item/list-item';
import { ClockPage } from '../pages/clock/clock';
import { StopwatchPage } from '../pages/stopwatch/stopwatch';
import { MinuteurPage } from '../pages/minuteur/minuteur';

// Pipes
import { TwoDigit } from '../app/pipes/two-digit';
import { MultiTen } from '../app/pipes/multiplicateur';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage, 
    ContactPage,
    ListItemPage,
    ClockPage,
    TwoDigit,
    StopwatchPage,
    MinuteurPage,
    MultiTen,

  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    ContactPage,
    ListItemPage,
    ClockPage,
    StopwatchPage,
    MinuteurPage,

  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



