import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { TimerInvert } from '../../app/invertTimer';
import { State } from '../../app/state';

@Component({
  selector: 'page-minuteur',
  templateUrl: 'minuteur.html'
})
export class MinuteurPage {

  private myTimer: TimerInvert = new TimerInvert();
  private myState: State = new State();
  private btnPlay: string = 'Démarrer';

  private myParamSec: number = 0;
  private myParamMin: number = 0;
  private myParamHrs: number = 0;

  constructor(public navCtrl: NavController) {
    
  }
  backward() {
    this.myTimer.reset();
    this.myState.setBackward();
    this.btnPlay = 'Démarrer';
  }

  play() {
    this.myTimer.start();
    this.myState.setPlay();
    this.btnPlay = 'Continuer';
  }

  stop() {
    this.myTimer.stop();
    this.myState.setStop();
  }

  setTimer(){
    this.myTimer.initTime(this.myParamHrs, this.myParamMin, this.myParamSec);
  }
}


