export class TimerInvert{
    // Properties
    private secondes: number = 0;
    private minutes: number = 0;
    private heures: number = 1;
    
    private secondesStock: number = 0;
    private minutesStock: number = 0;
    private heuresStock: number = 0;

    private timer;

    //Methods
    start(){
        this.timer = setInterval( () => {
            if (this.secondes == 0 && this.minutes == 0 && this.heures == 0){
                this.stop();
            } else {
                if (this.secondes == 0 && this.minutes > 0){
                    this.minutes--;
                    this.secondes = 60;
                }
                if (this.minutes == 0 && this.heures > 0){
                    this.heures--;
                    this.minutes = 59;
                    this.secondes = 60;
                }if (this.secondes > 0){
                    this.secondes--;
                }
            }
        }, 1000);
    }

    stop(){
        clearInterval(this.timer);
    }

    initTime(heuresParam: number, minutesParam: number, secondesParam: number){
        this.secondes = this.secondesStock = secondesParam;
        this.minutes = this.minutesStock = minutesParam;
        this.heures = this.heuresStock = heuresParam;
    }

    reset(){
        this.minutes = this.minutesStock;
        this.secondes = this.secondesStock;
        this.heures = this.heuresStock;
    }
}


