import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { Timer } from '../../app/timer';
import { State } from '../../app/state';

@Component({
  selector: 'page-stopwatch',
  templateUrl: 'stopwatch.html'
})
export class StopwatchPage {

  private myTimer: Timer = new Timer();
  private myState: State = new State();
  private btnPlay: string = 'Démarrer';

  constructor(public navCtrl: NavController) {

  }
  backward() {
    this.myTimer.reset();
    this.myState.setBackward();
    this.btnPlay = 'Démarrer';
  }

  play() {
    this.myTimer.start();
    this.myState.setPlay();
    this.btnPlay = 'Continuer';
  }

  stop() {
    this.myTimer.stop();
    this.myState.setStop();
  }
}



